.App {
  display: flex;
  justify-content: center;
}

.facebookLogin {
  position: absolute;
  right: 10px;
}

.button__facebook {
  display: flex;
  align-items: center;
  width: 180px;
  height: 28px;
  border: none;
  border-radius: 10px;
  background-color: #1877f2;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
}

.button__facebook img {
  width: 16px;
  margin: 0 5px 0 5px;
}

.card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card-title {
  font-weight: 600;
  margin: 5px 0 5px 0;
}

.card-logout {
  text-decoration: none;
  color: red;
  font-size: 14px;
  font-weight: 600;
}

@media (max-width: 500px) {
  .card-body .rounded {
    display: none;
  }
}

.button__goBack {
  position: absolute;
  left: 25px;
  text-decoration: none;
  color: black;
  font-size: 18px;
  font-weight: 700;
  font-family: "Roboto";
}

.button__goBack img {
  width: 13px;
  padding-right: 10px;
}

.character__container {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
}

.character__img {
  display: flex;
  justify-content: center;
}

.character__img img {
  border-radius: 50%;
  width: 300px;
  height: 300px;
}

.character__name {
  text-align: center;
  font-size: 48px;
  margin: 16px 0 0 0;
  font-family: "Roboto", sans-serif;
}

.character__about {
  text-align: center;
  font-size: 20px;
  color: #8e8e93;
  margin: 30px 0 25px 0;
  font-family: "Roboto";
  font-weight: 500;
}

.character__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.character__list li {
  border-bottom: 1px solid rgba(33, 33, 33, 0.08);
}

.character__list li p:first-of-type {
  font-size: 16px;
  font-weight: 700;
  font-family: "Roboto";
}

.character__list li p:last-child {
  color: #6e798c;
  font-size: 14px;
  font-family: "Roboto";
  font-weight: 400;
}

.imageCard {
  width: 240px;
  height: 240px;
  margin: 12px 10px 12px 10px;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2), 0px 3px 4px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.14);
  border-radius: 4px;
}

.imageCard:hover {
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 6px 8px rgba(0, 0, 0, 0.12),
    0px 4px 8px rgba(0, 0, 0, 0.14);
  transition: linear 0.2s;
}

.imageCard img {
  width: 240px;
  height: 168px;
}

.imageCard__title-name {
  margin: 12px 0 6px 16px;
  font-size: 18px;
  font-weight: 500;
  font-family: "Roboto", sans-serif;
}

.imageCard__title-species {
  margin: 0 0 0 16px;
  color: rgba(0, 0, 0, 0.6);
  font-family: "Roboto", sans-serif;
}

.home__list {
  margin-top: 38px;
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: center;
}

.home__list a {
  text-decoration: none;
  color: black;
}

.container {
  width: 1040px;
  height: 845px;
  margin-top: 40px;
}

.home__img {
  display: flex;
  justify-content: center;
}

.home__img img {
  width: 600px;
  height: 200px;
}

.home__input input {
  margin-top: 15px;
  width: 100%;
  box-sizing: border-box;
  border: 2px solid #ccc;
  border-radius: 8px;
  font-size: 16px;
  background-color: white;
  background-image: url(data:image/png;base64,);
  background-position: 10px 10px;
  background-repeat: no-repeat;
  padding: 12px 20px 12px 40px;
}

@media (max-width: 635px) {
  .home__img img {
    width: 312px;
    height: 104px;
  }
}

